:root {
    --bg-color: rgba(42, 71, 94, 1);
    --fg-color: rgba(27, 40, 56, 1);
}

@font-face {
    font-family: 'Connection';
    src: local('Connection'), url(../assets/ConnectionIi-2wj8.otf), format('opentype');
}

@keyframes appear-left {
    0% {
        transform: translateX(-20%);
        color: rgba(27, 40, 56, 1);
        text-shadow: rgba(27, 40, 56, 1) 0px 0px, rgba(27, 40, 56, 1) 0px 0px;
    }
    100% {
        transform: translateX(0);
        color: rgba(42, 71, 94, 1);
        text-shadow: rgb(245, 245, 245) 2px 2px, rgba(245, 245, 245, 1) -2px -2px;
    }
}

@keyframes appear-down {
    0% {
        transform: translateY(40%);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes disappear-up {
    0% {
    }
    100% {
        height: 0;
        padding: 0;
    }
}

.hide-me {
    animation-name: disappear-up;
    animation-duration: 1s;
    animation-iteration-count: 1;
    overflow: hidden;
    animation-fill-mode: forwards;
}

header {
    height: 100vh;
    display: block;
    align-items: center;
    justify-content: center;
    background-color: var(--fg-color);
    padding-top: 5%;
}

header > h1 {
    --shadow-size: 3px;
    --shawdow-color: whitesmoke;
    font-size: 9em !important;
    font-family: Connection, Arial, sans-serif !important;
    color: var(--bg-color);
    text-shadow: rgba(245, 245, 245, 1) 2px 2px, rgba(245, 245, 245, 1) -2px -2px;
    animation-name: appear-left;
    animation-duration: 1s;
    animation-iteration-count: 1;
}

.header-menu {
    display: block;
    text-align: center;
    animation-name: appear-down;
    animation-duration: 1s;
    animation-iteration-count: 1;
}

.header-menu li {
    display: block;
    text-align: center;
}

.header-menu button {
    --ss: 1px; /* shadow size */
    --sc: black; /* shadow color */
    font-size: 0.7em;
    color: var(--bg-color);
    text-shadow: 
        var(--sc) calc(var(--ss)) calc(var(--ss)),
        var(--sc) calc(-1 * var(--ss)) calc(var(--ss)),
        var(--sc) calc(var(--ss)) calc(-1 * var(--ss)),
        var(--sc) calc(-1 * var(--ss)) calc(-1 * var(--ss));
}

.header-menu button:hover {
    --ss: 1px; /* shadow size */
    --sc: lightgray; /* shadow color */
    text-shadow: 
        var(--sc) calc(var(--ss)) calc(var(--ss)),
        var(--sc) calc(-1 * var(--ss)) calc(var(--ss)),
        var(--sc) calc(var(--ss)) calc(-1 * var(--ss)),
        var(--sc) calc(-1 * var(--ss)) calc(-1 * var(--ss));
    background-color: var(--fg-color);
}